}

.bgimg {
    background-image: url(/static/media/vancouver_skyline_vec.40531492.svg);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-position: bottom;
    background-size: cover;
    /* filter: blur(2px); */
}
.footer-container {
    /* position: relative; */
